body {
  background: coral;
  }
* {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  }
.title{
  width: 100%;
  padding: 10px 0;
  text-align: center;
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 700;
  background: rgb(248, 229, 200);
  color: #793535;
}
.app {
  height: auto;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-direction: column;
  color: #793535;
  }
form{
  display: flex;
  margin-bottom: 20px;
}
.todo-list {
  margin: 8px;
  height: auto;
  width: 80%;

  align-items: center;
  color: #793535;
  font-size: 20px;
  background-color: rgb(248, 229, 200);
  padding: 10px;
  border-radius: 20px;
  }
li {
  float: left;
  list-style: none;
  overflow-wrap: anywhere;
  height: auto;
  width: 176px;
  margin: 0 10px;
  }
input{
  padding: 5px;
  color: darkcyan;
  border: 4px solid;
  border-radius: 10px;
  background-color: antiquewhite;
  font-size: 20px;
}
button {
  padding: 0 8px;
  margin: 0 4px;
  border-radius: 8px;
  color: darkcyan;
  background-color: sandybrown;
  cursor: pointer;
  font-size: 13px;
  }
button:hover {
  color: #793535;
}
button:active {
  background-color: antiquewhite;
  transform: translateY(2px);
}
.btns{
  float: right;
  margin-right: 10px;
  display: flex;
  justify-content: center;
}
p{
  color: antiquewhite;
  margin-top: 30px;
  bottom: 20px;
  width: 80%;
  position: relative;
  text-align: center;
}

@media screen and (min-width: 1500px ) {
  .todo-list {
    width: 50%;
  }
}

@media screen and (max-width: 500px ) {
  .btns{
    float: right;
    display: grid;
  }
  button{
    height: 30px;
    width: 100px;
    margin: 3px 0;
  }
  form button{
    margin-left: 5px;
    height: 40px;
    width: 80px;
  }
  li{
    float: left;
    text-align: center;
  }
  .todo-list{
    display: flex;
  }
}